<template>
    <div class="newCouponList">
        <navigation-bar  @onLeftClick="onLeftClick" pageName="我的优惠券" ref="navigation"></navigation-bar>
        <van-tabs v-model="tabIndex" :swipeable="swipeable" animated>
            <van-tab ref="vantTab" title="待使用" color="#FB5768" line-width="0.67rem" line-height="0.08rem">
                <div class="scrollY" ref="scrollContent" v-if="tabIndex == 0">
                    <van-pull-refresh v-model="isRefreshLoading" @refresh="onRefresh">
                        <div v-if="platformList.length>0||couponList.length>0">
                            <div class="topSpace"></div>
                            <div v-if="platformList.length>0">
                                <div class="couponTitleBox">
                                    <div class="couponTitle">平台红包</div>
                                    <div class="allCouponNumBox" @click="goToAllCouponList(1)">
                                        <div class="allCouponNum">查看全部（共{{platformAllTotal}}张）</div>
                                        <img class="allCouponIcon" src="@/assets/images/mall/mall_icon_more.png" alt="">
                                    </div>
                                </div>
                                <coupon-item :url="url" :lists="platformList"/>
                            </div>
                            <div v-if="couponList.length>0">
                                <div class="couponTitleBox">
                                    <div class="couponTitle">店铺优惠券</div>
                                    <div class="allCouponNumBox" @click="goToAllCouponList(2)">
                                        <div class="allCouponNum">查看全部（共{{couponAllTotal}}张）</div>
                                        <img class="allCouponIcon" src="@/assets/images/mall/mall_icon_more.png" alt="">
                                    </div>
                                </div>
                                <coupon-item :url="url" :lists="couponList"/>
                            </div>
                            <div class="topSpace"></div>
                        </div>
                        <div class="noList" v-if="couponList.length == 0&&platformList.length == 0&&isLoading>=2">
                            <img src="@/assets/images/default/no_coupon_img.png"/>
                            <div>暂无优惠券</div>
                        </div>
                        <div v-if="isNoNetwork">
                            <NetworkError></NetworkError>
                        </div>
                    </van-pull-refresh>
                </div>
            </van-tab>
            <van-tab  title="已使用" color="#FB5768" line-width="0.67rem" line-height="0.08rem">
                <div class="scrollY" ref="scrollContent" v-if="tabIndex == 1">
                    <van-pull-refresh v-model="isRefreshLoading" @refresh="onRefresh">
                        <div v-if="platformList.length>0||couponList.length>0">
                            <div class="topSpace"></div>
                            <div v-if="platformList.length>0">
                                <div class="couponTitleBox">
                                    <div class="couponTitle">平台红包</div>
                                    <div class="allCouponNumBox" @click="goToAllCouponList(1)">
                                        <div class="allCouponNum">查看全部（共{{platformAllTotal}}张）</div>
                                        <img class="allCouponIcon" src="@/assets/images/mall/mall_icon_more.png" alt="">
                                    </div>
                                </div>
                                <coupon-item :url="url" :lists="platformList"/>
                            </div>
                            <div v-if="couponList.length>0">
                                <div class="couponTitleBox">
                                    <div class="couponTitle">店铺优惠券</div>
                                    <div class="allCouponNumBox" @click="goToAllCouponList(2)">
                                        <div class="allCouponNum">查看全部（共{{couponAllTotal}}张）</div>
                                        <img class="allCouponIcon" src="@/assets/images/mall/mall_icon_more.png" alt="">
                                    </div>
                                </div>
                                <coupon-item  :url="url" :lists="couponList"/>
                            </div>
                            <div class="topSpace"></div>
                        </div>
                        <div class="noList" v-if="couponList.length == 0&&platformList.length == 0&&isLoading>=2">
                            <img src="@/assets/images/default/no_coupon_img.png"/>
                            <div>暂无优惠券</div>
                        </div>
                        <div v-if="isNoNetwork">
                            <NetworkError></NetworkError>
                        </div>
                    </van-pull-refresh>
                </div>
            </van-tab>
            <van-tab  title="已过期" color="#FB5768" line-width="0.67rem" line-height="0.08rem">
                <div class="scrollY" ref="scrollContent" v-if="tabIndex == 2">
                    <van-pull-refresh v-model="isRefreshLoading" @refresh="onRefresh">
                        <div v-if="platformList.length>0||couponList.length>0">
                            <div class="topSpace"></div>
                            <div v-if="platformList.length>0">
                                <div class="couponTitleBox">
                                    <div class="couponTitle">平台红包</div>
                                    <div class="allCouponNumBox" @click="goToAllCouponList(1)">
                                        <div class="allCouponNum">查看全部（共{{platformAllTotal}}张）</div>
                                        <img class="allCouponIcon" src="@/assets/images/mall/mall_icon_more.png" alt="">
                                    </div>
                                </div>
                                <coupon-item :url="url" :lists="platformList"/>
                            </div>
                            <div v-if="couponList.length>0">
                                <div class="couponTitleBox">
                                    <div class="couponTitle">店铺优惠券</div>
                                    <div class="allCouponNumBox" @click="goToAllCouponList(2)">
                                        <div class="allCouponNum">查看全部（共{{couponAllTotal}}张）</div>
                                        <img class="allCouponIcon" src="@/assets/images/mall/mall_icon_more.png" alt="">
                                    </div>
                                </div>
                                <coupon-item :url="url" :lists="couponList"/>
                            </div>
                            <div class="topSpace"></div>
                        </div>
                        <div class="noList" v-if="couponList.length == 0&&platformList.length == 0&&isLoading>=2">
                            <img src="@/assets/images/default/no_coupon_img.png"/>
                            <div>暂无优惠券</div>
                        </div>
                        <div v-if="isNoNetwork">
                            <NetworkError></NetworkError>
                        </div>
                    </van-pull-refresh>
                </div>
            </van-tab>
        </van-tabs>
    </div>
</template>

<script>
    import {getUserCouponList,getUserPlatformList,getUserNewPlatformList } from "../../services/orderListService";
    import NavigationBar from '@/common/currency/NavigationBar.vue';
    import couponItem from '@/page/couponList/couponItem'
    import NetworkError from '@/common/networkError/NetworkError'
    import { Toast, Dialog,ActionSheet  } from "vant";
    export default {
        name:"newCouponList",
        components:{
            NavigationBar,
            couponItem,
            NetworkError
        },
        data(){
            return{
                swipeable:true,
                tabIndex:0,
                statusList:[{
                    tabName:'未使用',
                    status:'01'
                },{
                    tabName:'已使用',
                    status:'02'
                },{
                    tabName:'已失效',
                    status:'03'
                }],
                url:['/newCouponList','我的优惠券列表'],
                isNoNetwork:false, //是否没有网络
                isLoading:0, //是否在加载中
                isRefreshLoading:false,
                couponList:[],
                couponAllTotal:0,
                platformList:[],
                platformAllTotal:0,
                scrollTop:0,
            }
        },
        mounted(){

        },
        activated(){
            this.$nextTick(()=>{
                let bodyHeight = document.documentElement.clientHeight || document.body.clientHeight;
                this.$refs.vantTab.$el.parentNode.parentNode.style.height = (bodyHeight-44-this.$stbHeight-50)+'px';
            });
            if(this.$route.params.isInit){
                this.tabIndex = 0;
                this.init();
            }else{
                this.$refs.scrollContent.scrollTop = this.scrollTop;
            }
        },
        deactivated(){

        },
        watch:{
            tabIndex:{
                handler(newName, oldName) {
                    this.init();
                },
            },
        },
        computed: {

        },
        methods:{
            init(loading = false){
                this.isLoading = 0;
                this.isNoNetwork = false;
                this.couponList = [];
                this.couponAllTotal = 0;
                this.platformList = [];
                this.platformAllTotal = 0;
                this.queryByUser();  //店铺优惠券
                if(loading){
                    this.queryByUserV33(); //平台优惠券
                }else{
                    this.queryByUserNewV33(); //平台优惠券
                }
            },
            queryByUser(){
                let tabIndex = this.tabIndex;
                const paramData = {
                    page:1,
                    rows:10,
                    ownerCode:this.$userInfo.OWNER_CODE,
                    status:this.statusList[this.tabIndex].status,
                }
                getUserCouponList(paramData).then(res => {
                    if (res.resultCode == 0) {
                        if(tabIndex==this.tabIndex){
                            this.couponList = [];
                            if(res.data.couponScopeList.length>0){
                                res.data.couponScopeList.map((couponItem,index)=>{
                                    if(index<3){
                                        this.couponList.push(couponItem)
                                    }
                                })
                            }
                            this.couponList.isShowBtn =  true;
                            this.isLoading += 1;
                        }
                        this.couponAllTotal = res.total;
                    }else{
                        this.isLoading += 1;
                    }
                }).catch(error => {
                    this.couponList = [];
                    this.isRefreshLoading = false;
                    this.isLoading = 0;
                    this.isNoNetwork = true;
                });
            },
            queryByUserV33(){
                let tabIndex = this.tabIndex;
                const paramData = {
                    page:1,
                    rows:10,
                    ownerCode:this.$userInfo.OWNER_CODE,
                    status:this.statusList[this.tabIndex].status,
                }
                getUserPlatformList(paramData).then(res => {
                    if (res.resultCode == 0) {
                        this.isRefreshLoading = false;
                        if(tabIndex==this.tabIndex){
                            this.platformList = [];
                            if(res.data.platformList.length>0){
                                res.data.platformList.map((couponItem,index)=>{
                                    if(index<3){
                                        this.platformList.push(couponItem);
                                    }
                                })
                            }
                            this.platformList.isShowBtn =  true;
                            this.isLoading += 1;
                        }
                        console.log('isLoading'+this.isLoading)
                        this.platformAllTotal = res.total;
                    }else{
                        this.isLoading += 1;
                    }
                }).catch(error => {
                    this.platformList = [];
                    this.isRefreshLoading = false;
                    this.isLoading = 0;
                    this.isNoNetwork = true;
                });
            },
            queryByUserNewV33(){
                let tabIndex = this.tabIndex;
                const paramData = {
                    page:1,
                    rows:10,
                    ownerCode:this.$userInfo.OWNER_CODE,
                    status:this.statusList[this.tabIndex].status,
                }
                getUserNewPlatformList(paramData).then(res => {
                    if (res.resultCode == 0) {
                        this.isRefreshLoading = false;
                        if(tabIndex==this.tabIndex){
                            this.platformList = [];
                            if(res.data.platformList.length>0){
                                res.data.platformList.map((couponItem,index)=>{
                                    if(index<3){
                                        this.platformList.push(couponItem);
                                    }
                                })
                            }
                            this.platformList.isShowBtn =  true;
                            this.isLoading += 1;
                        }
                        console.log('isLoading'+this.isLoading)
                        this.platformAllTotal = res.total;
                    }else{
                        this.isLoading += 1;
                    }
                }).catch(error => {
                    this.platformList = [];
                    this.isRefreshLoading = false;
                    this.isLoading = 0;
                    this.isNoNetwork = true;
                });
            },
            //查看全部
            goToAllCouponList(couponType){
                console.log(couponType)
                this.$router.push({
                    name: 'allCouponList',
                    params: {
                        couponType:couponType,
                        statusType:this.statusList[this.tabIndex].status
                    }
                })
            },
            //下拉刷新
            onRefresh(){
                this.init(true);
            },
            //返回键
            onLeftClick() {
                //用于app处理返回
                let backResult=this.$setRouterIndex();
                if(backResult){
                    this.$router.go(-1);
                }
            },
        },
        beforeRouteLeave(to, form, next) {
            this.scrollTop  = this.$refs.scrollContent.scrollTop;
            next();
        }
    }
</script>

<style scoped lang="scss">
    /deep/.navBar{
        box-shadow: none;
    }
    .network{
        background: #f9f9f9;
    }
    .couponTitleBox{
        width: 100%;
        padding: 0 15px;
        margin-bottom: 10px;
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .couponTitle{
            line-height: 20px;
            font-family: PingFangSC-Medium;
            font-size: 14px;
            color: #4A4A4A;
            letter-spacing: 0;
            font-weight: 600;
        }
        .allCouponNumBox{
            display: flex;
            align-items: center;
            .allCouponNum{
                line-height: 20px;
                font-family: PingFangSC-Regular;
                font-size: 14px;
                color: #333333;
                letter-spacing: 0;
            }
            .allCouponIcon{
                width: 6px;
                height: 11px;
            }
        }
    }
    .newCouponList{
        width: 100%;
        height: 100%;
        background-color: #f9f9f9;
        /*display: flex;*/
        /*flex-direction: column;*/
    }
    /deep/.van-tabs{
        height: 100%;
        .van-tabs__wrap{

            padding: 0 50px;
            background: #FFFFFF;
            height: 45px!important;
            border: 0;
            .van-tab{
                font-family: PingFangSC-Regular;
                font-size: 14px;
                color: #9A9A9A;
                letter-spacing: 0;
            }
            .van-tab--active{
                font-family: PingFangSC-Medium;
                color: #4A4A4A;
                font-weight: 600;
            }
            .van-tabs__line{
                width: 25px !important;
                height: 3px;
                border-radius: 4px;
                background: #FB5768;
            }
        }
        .van-tabs__wrap::after{
            border-width: 0;
        }
        .van-tabs__content{
            height: 100%;
            .van-tabs__track{
                height: 100%;
                .van-tab__pane-wrapper{
                    height: 100%;
                    .van-tab__pane{
                        height: 100%;
                        .scrollY{
                            height: 100%;
                            overflow-y: auto;
                            .van-pull-refresh{
                                min-height: 100%;
                            }
                        }
                    }
                }
            }
        }
    }
    .topSpace{
        width: 100%;
        height: 15px;
    }
    .noList {
        img {
            margin: 125px 100px 10px 100px;
            width: 180px;
            display: inline-block;
        }
        div {
            font-family: PingFangSC-Regular;
            font-size: 16px;
            color: #9A9A9A;
            letter-spacing: 0.06px;
            text-align: center;
            margin-top: 24px;
        }
    }
</style>
